.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
:root{
  --lightgray:#D9D9D9;
  --gray:#9c9c9c;
  --orange:#f48915;
  --darkgray:#464D53;
  --caloryCard:#656565;
  --planCard:linear-gradient(210.41deg,#fa5042 1.14%, #ffa739 100.75%);
  --appColor:#3c3f45;
}
.App{
  /* background-image: url(../src/pics/cover\ gym.jpg); */
  mix-blend-mode: overlay;
  background-color: var(--appColor);
  display: flex;
  flex-direction: column;
  gap: 10rem;
  overflow: hidden;
}
::-webkit-scrollbar{
  display: none;
}
.stroke-text{
  color: transparent;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: whitesmoke;
}
.btn{
  background-color: aliceblue;
  padding: 0.5rem;
  font-weight: bold;
  border: 4px transparent;
  transition: 300ms all;
  display: flex;
  align-items: center;
  justify-content: center;

}
.btn:hover{
  cursor: pointer;
}
.blur{
  background: rgb(253,120,43,0.69);
position: absolute;
border-radius: 50%;
filter: blur(120px);
z-index: -9;
}
